}

.register-username, .password, .email, .submit-button {
  background-color: transparent;
  transition: all 150ms linear;
}

.register-username:hover, .password:hover, .email:hover, .submit-button:hover {
  background-color: rgba(42, 60, 79, 0.3);
}

.link {
  padding: 3px;
  font-size: 13px;
}

#permission-container {
  overflow-y: scroll;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media screen and (max-width: 600px) {
  .permission-logo {
    margin: 5px auto 0;
